const state={
    cartNum:(localStorage.getItem('userAccounts'))?JSON.parse(localStorage.getItem('userAccounts')).cart_nums:'0',//
    balance:'0',//
    isLoading:false,
    starCore:0,//
    userInfo:(localStorage.getItem('userAccounts'))?JSON.parse(localStorage.getItem('userAccounts')):'0'
}
export default state;


