// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
// import FastClick from 'fastclick'
import router from './router';
import Vuex from 'vuex';
import App from './App';
import vconsole from './assets/js/vconsole';
// ajax 
import api from './assets/js/api';
Vue.prototype.$api=api;
// 
import regexp from './assets/js/regexp';
Vue.prototype.$regexp=regexp;

// 
import tools from './assets/js/tools';
Vue.prototype.$tools=tools;

Vue.use(Vuex);
import vueg from 'vueg';
import 'vueg/css/transition-min.css';

// 
const options={
    duration: '0.1',              //0.3
    firstEntryDisable: false,     //truefalse
    firstEntryDuration: '.6',     //.6
    forwardAnim: 'fadeInRight',   //fadeInRight
    backAnim: 'fadeInLeft',       //fedeInLeft
    sameDepthDisable: false,      //urlfalse
    tabs: [{
        name:'cartlist'
    },{
        name:'login'
    },{
        name:'accountindex'
    },{
        name:'login'
    }],                       //[]namename,apptabtab[1]tab[0]backAnimtab[1]tab[2]forwardAnim
    tabsDisable: false,           //truetabsfalse
    disable: false,               //falsetrue
};
Vue.use(vueg, router,options);
// FastClick.attach(document.body);
Vue.config.productionTip = false;
//vuex
import store from './store/index';
import VueCordova from 'vue-cordova';
Vue.use(VueCordova);
console.log(Vue.cordova);
Vue.config.productionTip=false;
/* eslint-disable no-new */
new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app-box');



